@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
/* Global Styles */
:root {
  --primary-color: #dc3545;
  --dark-color: #333333;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}

.alert {
  padding: 0.7rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: #f4f4f4;
  background: var(--light-color);
  color: #333;
}

.bg-light,
.badge-light {
  border: #ccc solid 1px;
}

.round-img {
  border-radius: 50%;
}

/* Forms */
input {
  margin: 1.2rem 0;
}

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

input[type='text'],
input[type='email'],
input[type='password'],
input[type='date'],
select,
textarea {
  /* display: block;
  width: 100%;
  padding: 0.4rem; */
  font-size: 1.2rem;
  border: 1px solid #ccc;
}

input[type='submit'],
button {
  font: inherit;
}

table th,
table td {
  padding: 1rem;
  text-align: left;
}

table th {
  background: #f4f4f4;
  background: var(--light-color);
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  z-index: 1;
  width: 100%;
  opacity: 0.9;
  margin-bottom: 1rem;
}
nav {
    text-align: center;
    background-color: #1C7ACA;
    color: white;
}

nav h1 {
    font-weight: 100;
}
#mystocks {
    text-align: center;
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 85%;

    margin-left:auto; 
    margin-right:auto;
}

#mystocks table {
    border: 1px solid #ddd;
}
  
#mystocks td, #mystocks th {
    /* border: 1px solid #ddd; */
    /* border: 0px solid #E1E1E1; */
    padding: 8px;
}
  
#mystocks tr:nth-child(even){background-color: #f2f2f2;}
  
#mystocks tr:hover {background-color: #ddd;}
  
#mystocks th {
    padding: 10px 20px;
    text-align: left;
    background-color: #1C7ACA;
    color: white;
}

#mystocks h2 {
    text-align: left;
    margin: 1em 0;
}

#mystocks button {
    background-color: red;
    padding: 0 20px;
    margin: 0 20px;
    color: white;

    outline: none;
}
#addstocks {
   text-align: center;
   font-family: "Roboto", Arial, Helvetica, sans-serif;
   border-collapse: collapse;
   width: 85%;

   margin-left:auto; 
   margin-right:auto;
}

#addstocks h2 {
   text-align: left;
   margin: 1em 0;
}

#addstocks div {
   display: inline-block;
   text-align: left;
   width: 50%;

   margin-top: 10px;
}

#addstocks div button {

   margin-right: 20px;
   padding: 1px 30px;
   border-radius: 10%;
   box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
   width: 25%;

}

#addstocks div button:focus {
   
   outline: none;
}

/* modal */

/* The Modal (background) */
.modal {
   /* display: none; Hidden by default */
   position: fixed; /* Stay in place */
   z-index: 1; /* Sit on top */
   padding-top: 100px; /* Location of the box */
   left: 0;
   top: 0;
   width: 100%; /* Full width */
   height: 100%; /* Full height */
   overflow: auto; /* Enable scroll if needed */
   background-color: rgb(0,0,0); /* Fallback color */
   background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
 }
 
 /* Modal Content */
 .modal-content {
   background-color: #fefefe;
   margin: auto;
   padding: 20px;
   border: 1px solid #888;
   width: 80%;
 }
 
 /* The Close Button */
 .close {
   color: #aaaaaa;
   float: right;
   font-size: 28px;
   font-weight: bold;
 }
 
 .close:hover,
 .close:focus {
   color: #000;
   text-decoration: none;
   cursor: pointer;
 }

.modal-content h3 {

   text-align: center;
}

.modal-input div {
   display: flex;
   justify-content: space-between;
   font-weight: 700;
}

.modal-input div input {

   width: 25%;
}

.modal-input div p {

   display: flex;

   align-items: center;
}

.modal-button div {

   display: flex;
   justify-content: center;
}
